// 後台登入頁面
<template>
  <div class="container my-5">
    <div class="row">
      <div class="col-4">
        <img src="data:image/jpeg;base64," alt="" class="login-img">
      </div>
      <div class="login">
        <form class="row justify-content-center" @submit.prevent="signIn">
          <div class="col-md-10">
            <h1 class="h3 mb-3 font-weight-normal">Please login</h1>
            <div class="mb-2">
              <label for="inputEmail" class="sr-only">Email address</label>
              <input
                type="email"
                id="inputEmail"
                class="form-control"
                placeholder="Email address"
                required
                autofocus
                v-model="user.username"
              />
            </div>
            <div class="mb-2">
              <label for="inputPassword" class="sr-only">Password</label>
              <input
                type="password"
                id="inputPassword"
                class="form-control"
                placeholder="Password"
                required
                v-model="user.password"
              />
            </div>

            <div class="text-end mt-4">
              <button class="btn btn-lg btn-primary btn-block" type="submit">
                Login
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 使用v-model綁定html的input
      user: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    signIn () {
      // 取得.env中的VUE_APP_API並串接api
      const api = `${process.env.VUE_APP_API}admin/signin`
      // axios的$http方法
      this.$http.post(api, this.user)
        .then((res) => {
          // if登入成功
          if (res.data.success) {
            const { token, expired } = res.data
            // 存入cookie與expired
            document.cookie = `timToken = ${token}; expires = ${new Date(expired)}`
            // 導向products頁面
            this.$router.push('/dashboard/products')
          }
        })
    }
  }
}
</script>

<style lang="scss">
@import "../assets/mixins.scss";
.login-img{
  @include pad(){
    display: none;
  }
}
.login{
  width: 60%;
  @include pad(){
    width: 95%;
    margin: 0 auto;
  }
}
</style>
